import React from 'react'
import { Link, } from "react-router-dom";
export default function Register() {
  return (
    <>
    <section className="login_form">
      <form action="" className="login_form_tag">


      <label htmlFor="">Full Name:</label>
        <input type="text" className='fullName_login'  placeholder='Enter your full name...'/>

        <label htmlFor="">Email:</label>
        <input type="email" className='email_login'  placeholder='Enter your email...'/>

        <label htmlFor="">Password:</label>
        <input type="password" className='passpord_login'  placeholder='Enter your password...'/>

       <button className="submit_register_btn">REGISTER</button>

        <Link to="/Login" className='forget_password'>Forgot password?</Link>
        <Link to="/Login" className='dont_have_account'>Already have an account?</Link>
      </form>
    </section>
    </>
  )
}
