import React from 'react'
import Card from "./CSS/Login.css";
import { Link } from "react-router-dom";
export default function Login() {
  return (
    <>
    <section className="login_form">
      <form action="" className="login_form_tag">


        <label htmlFor="">Email:</label>
        <input type="email" className='email_login'  placeholder='Enter your email...'/>

        <label htmlFor="">Password:</label>
        <input type="password" className='passpord_login'  placeholder='Enter your password...'/>

       <button className="submit_login_btn">LOGIN</button>

       
       <Link to="/ForgotPassword" className='forget_password'>Forget password?</Link>
        <Link to="/Register" className='dont_have_account'>Don't have an account?</Link>
      </form>

      
      
    </section>
    </>
  )
}
