import React from 'react'
import SingleProductPageCss from './CSS/SingleProductPage.css'

export default function SingleProduct(props) {
  return (
    <>
    <div className="container">
    <div className="product_image_slider">
      <img 
      src={props.ProducutImageLink}
      />
    </div>

    <div className="flex_direction">
    <div className="left_flex">
    <p className='singlePP_price'>{props.singleProductPrice}</p>
      <h3 className="singlePT">{props.singleProductTitle}</h3>
      <p className="singlePD">{props.singleProductDescription}
      </p>
    </div>

    <div className="right_flex">
      <div className="size_flex">
        <p>SELECT SIZE:</p>
        <button className="size">26</button>
        <button className="size">28</button>
        <button className="size">30</button>
        <button className="size">32</button>
        <button className="size">34</button>
        <button className="size">36</button>
        <button className="size">38</button>
        {/* <button className="size">40</button> */}
      </div>

<br />
        <div className="color_flex">
        <p>SELECT COLOR:</p>
        <button className="color">Red</button>
        <button className="color">Yellow</button>
        <button className="color">Green</button>
        <button className="color">Grey</button>
        <button className="color">Blue</button>
        {/* <button className="color">White</button> */}

        <div className="buy_cart_btn">
          <button className="cart_btn">Add to Cart</button>
          <button className="buy_now_btn">Buy Now</button>
        </div>

        </div>
      </div>
      </div>
    </div>
    </>
  )
}
